* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

html, body {
    font-family: 'Poppins',
    sans-serif;
    font-size: 18px;
}

#root {
    display: flex;
    justify-content: center;
    text-align: center;
    background-image: url(./images/smileyface.jpg);
}

.container {
    background-color: lightgreen;
    width: 600px;
    height: 600px;
    padding: 10px;
    border-radius: 0.5rem;
}

.btn {
    padding: 5px 10px;
    margin-top: 3rem;
    border-radius: 0.5rem;
    background-color: teal;
    color: black;
    font-size: 1rem;
    border: none;
    transition: ease-in-out 0.5s;
}

.btn:hover {
    color: yellow;
}

.joke {
    margin-top: 3rem;
    font-size: 1rem;
}